import { inject, observer } from "mobx-react"
import DeriNumberFormat from "../../../../utils/DeriNumberFormat";
import classNames from "classnames";
import './position.less'

function Position({lang,trading}){
  const columns = {
    symbol : {key : 'symbol'},
    direction : {key : 'direction'},
    position : [{key : 'volume',type : 'number', suffix :'',decimal : 4},
                {key : 'notional',type : 'number',suffix : ' BUSD'}],
    entryPrice : {key : 'averageEntryPrice',type : 'number',prefix : '$'},
    markPrice : {key : 'markPrice',type : 'number' ,prefix : '$'},
    marginUsage : {key : 'marginHeldBySymbol',type : 'number'},
    liquidityPrice : {key : 'liquidationPrice',type : 'number'},
    unrealizedPnl : [{key : 'unrealizedPnl',type : 'number', decimal : 4},
                    {key : 'unrealizedPnlRate',type : 'number', decimal : 2,suffix : '%'}],
    fundingFee : {key : 'fundingFee',type : 'number'},
    action : {}
  }
  const keys = Object.keys(columns)
  
  const close = symbol => {
    
  }
  return (
    <div className='m-position'>
      <div className='p-header'>
        {keys.map((key,index) => (
          <div key={index} className ='p-col'>{lang[key]}</div>
        ))}
      </div>
      <div className='p-body'>
        {trading.positions.map((position,i) => (
          <div className='p-row' key={i}>
            {keys.map((key,index) => {
              const col = columns[key]
              return (col.key || col.length) && (
                Array.isArray(col) 
                  ? 
                    <div key={index}>
                      {col.map((child,key) => (
                        <div key={key} className={classNames('p-col',position[`${child.key}Color`])}><DisplayColumn col={child} data ={position}/></div>
                      ))}
                    </div>
                  : 
                    <div className={classNames('p-col',position[`${key}Color`])} key={index}><DisplayColumn col={col} data = {position}/></div>
                
              )
            })}
            <div className='p-col' onClick={col => close(col)}>{lang['close']}</div>
          </div>
        ))}
        
      </div>
    </div>
  )
}

const DisplayColumn = ({col,data}) => {
  return (
    <>
      {col.type === 'number' 
        ? <DeriNumberFormat prefix={col.prefix} value={data[col.key]} suffix={col.suffix} decimalScale={col.decimal || 2}/> 
        : data[col.key]}
    </>
    )
}

export default inject('trading')(observer(Position))