import styled from 'styled-components'
import gaugeGraph from '../../../assets/img/gauge-graph.svg'
import DeriNumberFormat from '../../../utils/DeriNumberFormat'
import Button from '../../../components/Button/Button'
import { SECONDARY, DEPOSIT, WITHDRAW } from '../../../utils/Constants'
import UnderlineText from '../../../components/UnderlineText/UnderlineText'
import { show, hide } from 'react-functional-modal'
import MarginPanel from '../../../components/MarginPanel/MarginPanel'
import { observer, inject } from 'mobx-react';
import './accountInfo.less'
import {useAlert} from 'react-alert'


function AccountInfo({trading,wallet,lang}){
  const alert = useAlert()
  
  const showMarginPanel = (type) => {
    show(<MarginPanel type={type} lang={lang} wallet={wallet} pool={trading.pool} alert={alert}/> ,{
      key  : 'marginPanel',
      fading: true,
      style: {
        background: "rgba(0, 0, 0, 0.4)" ,
        zIndex : 1,
      }
    })
  }

  const closeMarginPanel = () => {
    hide('marginPanel')
  }

  return(
    <div className='account' >
      <div className='a-info'>
          <div className='content'>
            <div className='detail-graph'>
              <div className='detail'>
                <div className='title row'>{lang['account-info']}</div>
                <div className='row'>
                  <div><UnderlineText text={lang['dynamic-effective-balance']}/></div>
                  <div className='value'><DeriNumberFormat value={trading.accountInfo.dynEffMargin} decimalScale='2' suffix=' BUSD'/></div>
                </div>
                <div className='row'>
                  <div><UnderlineText text={lang['margin-usage']}/></div>
                  <div className='value'><DeriNumberFormat value={trading.accountInfo.marginUsage} suffix=' BUSD' decimalScale='2' /></div>
                </div>
                <div className='row'>
                  <div><UnderlineText text={lang['available-margin']}/></div>
                  <div className='value'><DeriNumberFormat value={trading.accountInfo.availableMargin} decimalScale='2' suffix=' BUSD'/></div>
                </div>
              </div>
              <div className='graph'>
                <img src ={gaugeGraph} alt=''/>
              </div>
          </div>
          <div className='info-footer'>
            <Button type={SECONDARY} icon='deposit' outline label={lang['deposit'].toUpperCase()} onClick={() => showMarginPanel(DEPOSIT)}/> 
            <Button outline type={SECONDARY} icon='withdraw' label={lang['withdraw'].toUpperCase()} onClick={() => showMarginPanel(WITHDRAW)}/>
          </div>
        </div>
      </div>
    </div>
  )
}
export default inject('wallet','trading')(observer(AccountInfo))