import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import UnderlineText from "../../../components/UnderlineText/UnderlineText";
import DeriNumberFormat from "../../../utils/DeriNumberFormat";
import { inject, observer } from 'mobx-react';
import Type from '../../../model/Type';

const Wrapper = styled.div`
display : flex;
height: 96px;
align-items: center;
justify-content: space-between;
margin: 0 32px;

.m-col {
  display : flex;
  align-items: flex-start;
  flex-direction: column;
  margin-right : 48px;
  justify-content: space-between;
  .col-value {
    font-size : 20px;
    color : #E0ECFF;
    height :24px;
  }
}
`
function TradeInfo({trading,lang}){
  const fileds = [
    {text : lang['market-price'],value : '',prefix : '$',decimal : 2,type : 'number'},
    {text : lang['oracle-price'],value : '',prefix : '$',decimal : 2,type : 'number'},
    {text : lang['dvol'],value : '',suffix : '%',decimal : 2,type : 'number'},
    {text : lang['daily-funding'],value : '',suffix : '%',decimal : 2,type : 'number'},
    {text : lang['market-change'],value : '',type : 'number'},
    {text : lang['total-net-position'],value : '',decimal : 2,type : 'number'},
    {text : lang['margin-req'],value : ''},
    {text : lang['fee'],value : ''}
  ]
  const [columns, setColumns] = useState(fileds)
  useEffect(() => {
    if(trading.fundingRate){
      const cols = [
        {text : lang['markPrice'],value : trading.markPrice,prefix : '$',decimal : 2,type : 'number'},
        {text : lang['oracle-price'],value : trading.index,prefix : '$',decimal : 2,type : 'number'},
        {text : lang['daily-funding'],value : trading.dailyFunding,decimal : 4,type : 'number'},
        {text : lang['market-change'],value : '--',type : 'number'},
        {text : lang['total-net-position'],value : trading.netPositionNutual,decimal : 4,type : 'number'},
        {text : lang['margin-req'],value : trading.marginReqs},
        {text : lang['fee'],value : trading.transactionFee},
      ]
      if(Type.isOption){
        cols.splice(2,0,{text : lang['dvol'],value : trading.volatility,suffix : '%',decimal : 2,type : 'number'})
      }
      setColumns(cols)
    }
  }, [trading.fundingRate,trading.volatility,trading.transactionFee,trading.marginReqs,trading.netPositionNutual,trading.dailyFunding])
  return(
    <Wrapper>
      {columns.map((col,index) => (
        <div className='m-col' key={index}>
          <UnderlineText text={col.text}/>
          <div className='col-value'>
            { col.type === 'number' 
            ? <DeriNumberFormat  value = {col.value} prefix={col.prefix} suffix={col.suffix} decimalScale={col.decimal || 2}/> 
            : col.value }
          </div>
        </div>
      ))}
  </Wrapper>
  )
}
export default inject('trading')(observer(TradeInfo))