import Tab from "../../../../components/Tab/Tab"
import { FUTURE, FUTURE_INNO, FUTURE_OPEN } from "../../../../lib/web3js/shared"
import DeriNumberFormat from "../../../../utils/DeriNumberFormat"
import Arrow from "../../../../components/Arrow/Arrow"
import { ARROW_UP, VERSION_V2, VERSION_V2_LITE, VERSION_V2_OPEN } from "../../../../utils/Constants"
import btcIcon from '../../../../assets/img/btc.svg'
import './symbolPanel.less'
import { inject, observer } from "mobx-react"
import Type from "../../../../model/Type"
import {
  Accordion,
  AccordionItem,
  AccordionItemHeading,
  AccordionItemButton,
  AccordionItemPanel,
} from 'react-accessible-accordion';


function SymbolPanel({trading,lang,symbols = []}){
  const onSelect = info => {
    trading.onSymbolChange(info)
  }
  return (
    <div className='symbol-panel'>
      <div className='sp-main'>
        {Type.isFuture ? 
          <FuturePanel lang={lang} symbols={symbols} trading={trading} onSelect={onSelect}/>
        :
          <OptionPanel symbols={symbols} onSelect={onSelect}/>
        }
      </div>
    </div>
  )
}


function FuturePanel({lang ,symbols = [],onSelect}){
  const tabSet = {
    [lang['main']] : {
      content :  <SymbolList symbols={symbols} version={VERSION_V2} onSelect={onSelect}/>
    },
    [lang['inno']] : {
      content : <SymbolList symbols={symbols}  version={VERSION_V2_LITE} onSelect={onSelect}/>
    },
    [lang['open']] : {
      content : <SymbolList symbols={symbols} version={VERSION_V2_OPEN} onSelect={onSelect}/>
    }
  }
  return (
    <Tab tabSet={tabSet} activeBgColor='rgba(255, 255, 255, 0.1)' className='future-zone-tab'/>
  )
}

const OptionPanel = ({symbols = {},onSelect}) => {
  const keys = Object.keys(symbols);
  return (
    <Accordion allowMultipleExpanded allowZeroExpanded preExpanded={keys} className='option-symbols'>
      {keys.map(key => (
        <AccordionItem uuid={key}>
          <AccordionItemHeading>
            <AccordionItemButton>{key}</AccordionItemButton>
          </AccordionItemHeading>
          <AccordionItemPanel>
            {symbols[key].map(info => {
              return (
                <div className='option-row' onClick={e => onSelect(info)}><img src={btcIcon} alt=''/>{info.symbolDisplay}</div>
              )
            })}
          </AccordionItemPanel>
        </AccordionItem>
      ))}
    </Accordion>)
  
}

function SymbolList({symbols,version,onSelect}){
  const filterSymbols = symbols.filter(s => s.version === version)
  return (
    <div className='symbols'>
      {filterSymbols.map((info,index) => {
        return (
          <div className='symbol-row' key={index} onClick={() => onSelect(info)}>
            <div className='ss-name'><img src={btcIcon} alt=''/>{info.symbolDisplay}</div>
            <div className='ss-value'>
              <div className='ss-value-detail'>
                <DeriNumberFormat prefix='$' value={55320}/> <Arrow direction={ARROW_UP}/> <DeriNumberFormat value={0.2} suffix='%' />
              </div>
            </div>
          </div>)
      })}
    </div>
  )}

export default inject('trading')(observer(SymbolPanel))