import { ARROW_UP } from "../../utils/Constants";
import arrowUpIcon from '../../assets/img/rate-up-icon.svg'
import arrowDonwIcon from '../../assets/img/rate-down-icon.svg'

export default function Arrow({direction = ARROW_UP}) {
  return direction === ARROW_UP ? <img src={arrowUpIcon} alt=''/> : <img src={arrowDonwIcon} alt=''/>
}