import { inject ,observer} from "mobx-react"
import './history.less'
import DeriNumberFormat from "../../../../utils/DeriNumberFormat"
import dateFormat from 'date-format'
import classNames from "classnames"

function History({lang,trading}){
  const colums = [
    {label : lang['time'],key : 'time' ,format : value => dateFormat.asString('yyyy-MM-dd hh:mm:ss',new Date(parseInt(value)))},
    {label : lang['direction'],key : 'direction',format : value => value === 'SHORT' ? lang['sell'] : lang['buy']},
    {label : lang['symbol'],key : 'symbol'},
    {label : lang['entryPrice'],key : 'price',type : 'number'},
    {label : lang['volume'],key : 'volume',type : 'number'},
    {label : lang['notional'],key : 'notional',type : 'number'},
    {label : lang['transaction-fee'],key : 'transactionFee',type : 'number'}
  ]

  return (
    <div className='m-history'>
      <div className='h-header'>
        {colums.map((col,index) => (
          <div key={index} className='h-col'>{col.label}</div>
        ))}
      </div>
      <div className='h-body'>
        {trading.history.map(h => (
          <div className='h-row'>
            {colums.map(col => {
              const value = col.format ? col.format(h[col.key]) : h[col.key]
              const clazz = classNames('h-col',value)
              return <div className={clazz}>{col.type === 'number' ? <DeriNumberFormat value={value} decimalScale={col.decimal || 2} /> : value}</div>
            }
              
            )}
          </div>
        ))}
      </div>
    </div>
  )
}
export default inject('trading')(observer(History))