import styled from 'styled-components'
import { inject, observer } from 'mobx-react'

const Wrapper = styled.div`
  margin : 24px 0 0 24px;
  .c-title {
    color : #E0ECFF;
    font-size : 14px;
    height: 40px;
  }
  .c-row {
    font-size : 12px;
    height: 24px;
    display: flex;
    justify-content: space-between;
  }
  .c-value {
    color : #E0ECFF;
    margin-right :24px;
  }
`
function ContractInfo({lang,trading}){
  const rows = [
    {label : lang['base-token'],key : 'baseToken'},
    {label : lang['symbol'],key : 'symbol'},
    {label : lang['min-trade-volume'],key : 'multiplier'},
    {label : lang['funding-rate-coefficent'],key : 'fundingRateCoefficient' ,suffix : '%'},
    {label : lang['min-initial-margin-ratio'],key : 'minInitialMarginRatio'},
    {label : lang['min-maintenance-margin-ratio'],key : 'minMaintenanceMarginRatio'},
    {label : lang['transaction-fee'],key : 'feeRatio'}
  ]
  return (
    <Wrapper>
      <div className='c-title'>{lang['contract-info']}</div>
      {rows.map((row,index) => (
        <div className='c-row' key={index}>
          <div>{row.label}</div>
          <div className='c-value'>{trading.contract[row.key]}</div>
        </div>
      ))}
    </Wrapper>
  )
}
export default inject('trading')(observer(ContractInfo))