import SymbolInfo from "./SymbolInfo";
import styled from "styled-components";
import SymbolPanel from "./SymbolPanel";
import ReactTooltip from 'react-tooltip'
import './symbolArea.less'
import { inject, observer } from "mobx-react";

function SymbolArea({trading,lang}){
  return (
    <div className='sa-main'>
      <SymbolInfo  lang={lang} pool={trading.pool || {}} markPrice={trading.markPrice}/>
      <ReactTooltip id='symbol-panel' effect='solid' place='bottom' scrollHide={false} className='s-react-tooltip' type="info" globalEventOff='click' backgroundColor='#203B60' clickable >
        <SymbolPanel lang={lang} pool={trading.pool} symbols={trading.symbols}/>
      </ReactTooltip>
    </div>
  )
}
// export default SymbolArea;
export default inject('trading')(observer(SymbolArea))