import React, { useState,useEffect } from 'react'
import RcSlider,{SliderTooltip} from 'rc-slider'
import 'rc-slider/assets/index.css';
// background : 'linear-gradient(270deg, #CE5E5E 9.38%, #CE5E5E 82.22%, #59AE99 89.24%, #59AE99 100%)'
export default function Slider({start,min,max,value,defaultValue,onSlider,className,trackStyle = {},railStyle = { },handleStyle = {}}){
  return (
    <RcSlider
        start={start}
        min={min}
        max={max}
        value={value}
        className={className}
        onChange={onSlider}
        defaultValue={defaultValue}
        trackStyle={{...trackStyle}}
        handleStyle={{...handleStyle}}
        railStyle={{...railStyle}}
      />
  )
}